import React from 'react';
import s from './MyPosts.module.css';
import Post from "./Post/Post";
import {Field, reduxForm} from "redux-form";
import {maxLengthCreator, required} from "../../../utils/validators/validators";
import {Textarea} from "../../common/FormsControls/FormsControls";

const maxLength10 = maxLengthCreator(10);

const MyPosts = React.memo(props => {
    let PostsElement =
        [...props.Posts]
            .reverse()
            .map(P => <Post message={P.message} key={P.id} likesCounts={P.likesCounts}/>)

    let addNewPost = (value) => {
        props.addPost(value.newPostElement);
    }

    return (
        <article className={s.myPosts}>
            <div>
                <div>
                    My posts
                </div>
                <AddPostsFormRedax onSubmit={addNewPost}/>
            </div>
            {PostsElement}
        </article>

    )
});

let AddPostForm = (props) => {
    return (
        <form onSubmit={props.handleSubmit}>
            <div>
                <Field component={Textarea} name={'newPostElement'} placeholder={'Enter your post'}
                       validate={[required, maxLength10]}/>
            </div>
            <button>add post</button>
        </form>
    )
}

const AddPostsFormRedax = reduxForm({form: 'myPostsAddPostForm'})(AddPostForm);

export default MyPosts;